import Vue from 'vue'
import VueResource from 'vue-resource'
Vue.use(VueResource)

const previousRequestMap = {}
const commonOpts = {
  // emulateJSON: true,
  before(request) {
    const key = Vue.url(request.url, request.params)
    if (previousRequestMap[key]) {
      previousRequestMap[key].abort()
    }

    previousRequestMap[key] = request
  }
}

const resource = Vue.resource
export const login = resource('auth/credentials/client', {}, {}, { _showLoadingStatus: true, ...commonOpts }) // 
export const wxOpenID = resource('wx/open_id', {}, {}, { _showLoadingStatus: true, ...commonOpts }) // openidcode
export const captcha = resource('captcha', {}, {}, { _showLoadingStatus: true, ...commonOpts }) // 
export const validateOldPhone = resource('verify/old_phone', {}, {}, { _showLoadingStatus: true, ...commonOpts }) // 
export const changePhone = resource('change/phone', {}, {}, { _showLoadingStatus: true, ...commonOpts }) // 
export const userSelf = resource('users/self', {}, {}, { _showLoadingStatus: true, ...commonOpts }) // 
export const bankCardInfo = resource('bank_cards/info', {}, {}, { _showLoadingStatus: true, ...commonOpts }) // 
export const bankCards = resource('bank_cards{/id}', {}, {}, { _showLoadingStatus: true, ...commonOpts }) // 
export const supportBankCards = resource('bank_cards/support_cards', {}, {}, { _showLoadingStatus: true, ...commonOpts }) // 
export const bankCardDelete = resource('bank_cards/unbind', {}, {}, { _showLoadingStatus: true, ...commonOpts }) // 
export const bankCardDefault = resource('bank_cards/default', {}, {}, { _showLoadingStatus: true, ...commonOpts }) // 
export const contractReturn = resource('contracts/return', {}, {}, { _showLoadingStatus: true, ...commonOpts }) // 
export const contractInitial = resource('contracts/initial', {}, {}, { _showLoadingStatus: true, ...commonOpts }) // 
export const contractUpdate = resource('contracts/update', {}, {}, { _showLoadingStatus: true, ...commonOpts }) // 
export const contractLoan = resource('contracts/loan', {}, {}, { _showLoadingStatus: true, ...commonOpts }) // 
export const selfContracts = resource('self/contracts{/id}', {}, {}, { _showLoadingStatus: true, ...commonOpts }) // 
export const contractReset = resource('contracts/give_up', {}, {}, { _showLoadingStatus: true, ...commonOpts }) // 
export const contractSign = resource('contracts/sign', {}, {}, { _showLoadingStatus: true, ...commonOpts }) // 
export const repayments = resource('repayments', {}, {}, { _showLoadingStatus: true, ...commonOpts }) // 
export const repaymentError = resource('repayment/reason', {}, {}, { _showLoadingStatus: true, ...commonOpts }) // 
export const systemPrivileges = resource('system/privileges', {}, {}, { _showLoadingStatus: true, ...commonOpts }) // 



